import React from "react";
import { Input, Select, Icon, message, DatePicker } from 'antd';
import locale from 'antd/lib/date-picker/locale/zh_CN';
import moment from 'moment';
import { inject, observer } from 'mobx-react/index'

import { isAuthenticated } from "../../utils/Session";
import api from "../../api/api";
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import './index.css'
import 'moment/locale/zh-cn';
moment.locale('zh-cn');

const Option = Select.Option;
const Search = Input.Search;
const getSvgIcon = (style = {}, text) => {

    const path = 'M715.8 493.5L335 165.1c-14.2-12.2-35-1.2-35 18.5v656.8c0 19.7 20.8 30.7 35 18.5l380.8-328.4c10.9-9.4 10.9-27.6 0-37z';
    const path1 = 'M840.4 300H183.6c-19.7 0-30.7 20.8-18.5 35l328.4 380.8c9.4 10.9 27.5 10.9 37 0L858.9 335c12.2-14.2 1.2-35-18.5-35z'
    return (
        <i style={style}>
            <svg
                viewBox="0 0 1024 1024"
                width="1em"
                height="1em"
                fill="currentColor"
                style={{ verticalAlign: '-.125em' }}
            >
                <path d={path1} />
            </svg>
        </i>
    );
};
let clearTime = ''
@inject('headerStore') @observer
class Index extends React.Component {
    constructor(props) {
        super(props)
        this.props.headerStore.setCurrunt(this.props.location.pathname)

    }
    state = {
        startValue: null,
        endTime: null,
        endOpen: false,
        backUserId: 1,
        list: [],
        total: 0,
        pageNo: 1,
        pages: 1,
        userType: [{
            'id': 0,
            name: '全部'
        }, {
            'id': 1,
            name: '员工'
        }, {
            'id': 2,
            name: '游客'
        }],
        type: 0,
        value: '',
        time: '',
        isSearch: false
    }
    componentWillMount() {
        let userInfo = JSON.parse(isAuthenticated().split('?')[0])
        this.setState({
            backUserId: userInfo.backUserId,
            startValue: moment('1970/01/01'),
            endTime: moment()
        })
        this.getList(moment('1970/01/01').format('YYYY/MM/DD HH:mm:ss'), moment().format('YYYY/MM/DD HH:mm:ss')).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    list: res.data.list,
                    total: res.data.total,
                    pageNo: res.data.pageNumber,
                    pages: res.data.pages
                })
            } else {
                message.warning(res.data.errorMessage)
            }
        })
    }
    getList = async (startTime, endTime, type = '', keyword = '', pageNo = 1, pageSize = 10) => {
        let res = api('getUserList', 'post', {
            "endTime": endTime,
            "keyword": keyword,
            "pageNo": pageNo,
            "pageSize": pageSize,
            "startTime": startTime,
            "type": type
        })
        return res
    }
    componentDidMount() {
        NProgress.done()
    }
    componentWillUnmount() {
        NProgress.start()
    }
    onSearch = (value) => {
        console.log(value)
        clearTimeout(clearTime)
        this.setState({
            startValue: moment('1970/01/01'),
            endTime: moment(),
            value: value,
            isSearch: true
        })
        this.getList(moment('1970/01/01').format('YYYY/MM/DD HH:mm:ss'), moment().format('YYYY/MM/DD HH:mm:ss'), '', value).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    list: res.data.list,
                    total: res.data.total,
                    pageNo: res.data.pageNumber,
                    pages: res.data.pages,
                    isSearch:false
                })
            } else {
                message.warning(res.data.errorMessage)
            }
        })

    }
    handleChange = (val) => {
        console.log(val);
        this.setState({
            type: val
        })
        this.getList(moment(this.state.startValue).format('YYYY/MM/DD HH:mm:ss'), moment(this.state.endTime).format('YYYY/MM/DD HH:mm:ss'), val == 0 ? '' : val, this.state.value).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    list: res.data.list,
                    total: res.data.total,
                    pageNo: res.data.pageNumber,
                    pages: res.data.pages
                })
            } else {
                message.warning(res.data.errorMessage)
            }
        })
    }
    onStartChange = (val) => {
        console.log(val);

        this.getList(moment(val).format('YYYY/MM/DD HH:mm:ss'), moment(this.state.endTime).format('YYYY/MM/DD HH:mm:ss'), this.state.type == 0 ? '' : this.state.type, this.state.value).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    list: res.data.list,
                    total: res.data.total,
                    pageNo: res.data.pageNumber,
                    pages: res.data.pages
                })
            } else {
                message.warning(res.data.errorMessage)
            }
        })
    }
    onEndChange = (val) => {
        console.log(val);
        this.getList(moment(this.state.startValue).format('YYYY/MM/DD HH:mm:ss'), moment(val).format('YYYY/MM/DD HH:mm:ss'), this.state.type == 0 ? '' : this.state.type, this.state.value).then(res => {
            if (!res.data.errorMessage) {
                this.setState({
                    list: res.data.list,
                    total: res.data.total,
                    pageNo: res.data.pageNumber,
                    pages: res.data.pages
                })
            } else {
                message.warning(res.data.errorMessage)
            }
        })
    }
    onChange = e => {
        console.log(e.target.value);
        let time = new Date().getTime()
        // if(e.target.value)
        let value = e.target.value

        clearTimeout(clearTime)

        if (!this.state.isSearch) {
            clearTime = setTimeout(() => {
                this.setState({
                    value: value,
                    startValue: moment('1970/01/01'),
                    endTime: moment(),
                    value: value,
                    type: 0
                })
                this.getList(moment('1970/01/01').format('YYYY/MM/DD HH:mm:ss'), moment().format('YYYY/MM/DD HH:mm:ss'), '', value).then(res => {
                    if (!res.data.errorMessage) {
                        this.setState({
                            list: res.data.list,
                            total: res.data.total,
                            pageNo: res.data.pageNumber,
                            pages: res.data.pages
                        })
                    } else {
                        message.warning(res.data.errorMessage)
                    }
                })
                clearTimeout(clearTime)
            }, 2000);
        }
    }
    setPrev = () => {
        if (this.state.pageNo == 1) {
            return
        } else {

            this.getList(moment(this.state.startValue).format('YYYY/MM/DD HH:mm:ss'), moment(this.state.endTime).format('YYYY/MM/DD HH:mm:ss'), this.state.type == 0 ? '' : this.state.type, this.state.value, this.state.pageNo - 1).then(res => {
                this.setState({
                    pageNo: this.state.pageNo - 1
                })
                if (!res.data.errorMessage) {
                    this.setState({
                        list: res.data.list,
                        total: res.data.total,
                        pages: res.data.pages

                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            })
        }
    }
    setNext = () => {
        if (this.state.pageNo == this.state.pages) {
            return
        } else {

            this.getList(moment(this.state.startValue).format('YYYY/MM/DD HH:mm:ss'), moment(this.state.endTime).format('YYYY/MM/DD HH:mm:ss'), this.state.type == 0 ? '' : this.state.type, this.state.value, this.state.pageNo + 1).then(res => {
                this.setState({
                    pageNo: this.state.pageNo + 1
                })
                if (!res.data.errorMessage) {
                    this.setState({
                        list: res.data.list,
                        total: res.data.total,
                        pages: res.data.pages

                    })
                } else {
                    message.warning(res.data.errorMessage, 2)
                }
            })
        }
    }
    render() {
        const { startValue, endTime, endOpen } = this.state;
        return (
            <div id="user">
                <div className="content">
                    <Search
                        placeholder="搜索"
                        onSearch={value => this.onSearch(value)}
                        style={{ width: '22.07%' }}
                        onChange={this.onChange}


                    />
                    <div className="head">
                        <div className='time'>
                            <DatePicker
                                disabledDate={this.disabledStartDate}
                                format="YYYY年MM月DD日"
                                value={startValue}
                                onChange={this.onStartChange}
                                locale={locale}
                                suffixIcon={getSvgIcon()}

                            />
                            至
                        <DatePicker
                                disabledDate={this.disabledEndDate}
                                format="YYYY年MM月DD日"
                                onChange={this.onEndChange}
                                value={endTime}
                                locale={locale}
                                suffixIcon={getSvgIcon()}
                            />
                        </div>
                        <div className="dept">


                            <Select defaultValue={0} style={{ width: 183 }} value={[this.state.type]} onChange={this.handleChange}>
                                {
                                    this.state.userType.length > 0 && this.state.userType.map(res => {
                                        return (
                                            <Option key={res.id} value={res.id}>{res.name}</Option>
                                        )
                                    })
                                }

                            </Select>


                        </div>
                    </div>
                    <div className="list">
                        <div className="list_head flexBox">
                            <div className="name flex">
                                姓名
                        </div>
                            <div className="tel flex">
                                手机号码
                        </div>
                            <div className="type flex">
                                身份
                        </div>
                            <div className="time flex">
                                注册时间
                        </div>
                        </div>
                        {
                            this.state.list.map((res, index) => {
                                if (index >= (this.state.pageNo - 1) * 10 && index < (this.state.pageNo) * 10) {
                                    return (
                                        <div className="list_info flexBox" key={res.id}>
                                            <div className="name flex">
                                                {res.name}
                                            </div>
                                            <div className="tel flex">
                                                {res.telnum}
                                            </div>
                                            <div className="stype flex" >
                                                {res.isEmp ? '员工' : '游客'}
                                            </div>
                                            <div className="time flex">
                                                {moment(res.createTime).format('YY年MM月DD日')}
                                            </div>
                                        </div>
                                    )
                                }
                            })
                        }
                        {
                            this.state.list.length==0?<div className='noData'>
                                    <img src={require('./../../assets/images/img-nodata.png')} alt=""/>
                                    <p>暂无相关文章</p>
                                </div>:''
                        }
                        {
                            this.state.list.length <= 10 ? '' : <div className='pagination'>
                                <span onClick={this.setPrev}>
                                    <Icon type="caret-left" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                                </span>
                                <span>
                                    {this.state.pageNo}/{Math.ceil(this.state.list.length / 10)}
                                </span>
                                <span onClick={this.setNext}>
                                    <Icon type="caret-right" style={{ 'color': '#CCCCCC', 'fontSize': '16px' }} />
                                </span>
                            </div>
                        }
                    </div>

                </div>
            </div>
        )
    }
}
export default Index